import React from 'react'

const Front = (props) => (

    <div className="front__card">
        <img src={`${props.info.thumbNail}`} alt=""/>
        <p>{props.info.date}</p>
        <h3>{props.info.title}</h3>
        {props.tag.map((item,index) => 
            <p>{props.tag[item]}</p> 
            
            )}

    </div>

)

export default Front;